import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from '@/plugins/vuetify'

import { VBScrollspyPlugin, BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import VueTypedJs from 'vue-typed-js'

import 'bootstrap'

import AOS from 'aos'
import 'aos/dist/aos.css'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faTwitter, faGithubAlt, faLinkedinIn, faSkype, faGithub, faLinkedin } from '@fortawesome/free-brands-svg-icons'
import { } from '@fortawesome/free-regular-svg-icons'
import { faStar, faHome, faUser, faFileAlt, faEnvelope, faBriefcase, faBars, faTimes, faSitemap, faDatabase, faShareAlt, faColumns, faChevronUp, faExternalLinkAlt } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import VueParticles from 'vue-particles'

// Scroll animate
const VueScrollTo = require('vue-scrollto')
Vue.use(VueScrollTo)

// Font Awesome
library.add(faTwitter, faGithubAlt, faLinkedinIn, faEnvelope, faSkype, faHome, faUser,
  faFileAlt, faBriefcase, faGithub, faBars, faTimes, faSitemap, faDatabase, faShareAlt, faColumns, faLinkedin, faChevronUp, faStar, faExternalLinkAlt)
Vue.component('font-awesome-icon', FontAwesomeIcon)

// Bootstrap Vue
Vue.use(BootstrapVue, VBScrollspyPlugin)
Vue.use(IconsPlugin)

// Typed.js
Vue.use(VueTypedJs)

// AOS
Vue.use(AOS)
AOS.init()

// Particles js
Vue.use(VueParticles)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
