<template>
  <div>
    <button type="button" class="mobile-nav-toggle d-xl-none" @click="iconToggle">
      <font-awesome-icon :icon="icon" size="lg" />
    </button>

    <fixed-header>
        <div class="navbar" :class="{'slideIn' : icon_toggle === true}">
          <b-nav vertical class="w-25" v-b-scrollspy="500">
            <b-nav-item href="#hero" v-scroll-to="'#hero'">
              <font-awesome-icon :icon="['fas', 'home']" size="lg" style="margin-left: -1px;" />
              <span>Home</span>
            </b-nav-item>
            <b-nav-item href="#services" v-scroll-to="'#services'">
              <font-awesome-icon :icon="['fas', 'briefcase']" size="lg" style="margin-left: 1px;" />
              <span>Services</span>
            </b-nav-item>
            <b-nav-item href="#resume" v-scroll-to="'#resume'">
              <font-awesome-icon :icon="['fas', 'file-alt']" size="lg" style="margin-left: 3px;" />
              <span>Resume</span>
            </b-nav-item>
            <b-nav-item href="#portfolio" v-scroll-to="'#portfolio'">
              <font-awesome-icon :icon="['fab', 'github']" size="lg" />
              <span>Portfolio</span>
            </b-nav-item>
            <b-nav-item href="#contact" v-scroll-to="'#contact'">
              <font-awesome-icon :icon="['fas', 'envelope']" size="lg" style="margin-left: 1px;" />
              <span>Contact</span>
            </b-nav-item>
          </b-nav>
        </div>
    </fixed-header>
  </div>
</template>

<script>
import FixedHeader from 'vue-fixed-header'
export default {
  components: {
    FixedHeader
  },
  data () {
    return {
      icon: ['fas', 'bars'],
      icon_toggle: true,
      windowWidth: 0
    }
  },
  methods: {
    iconToggle () {
      this.icon = this.icon_toggle ? ['fas', 'bars'] : ['fas', 'times']
      this.icon_toggle = !this.icon_toggle
    }
  },
  mounted () {
    this.windowWidth = window.innerWidth
    window.addEventListener('resize', () => {
      this.windowWidth = window.innerWidth
    })
  },
  watch: {
    windowWidth () {
      this.icon_toggle = !(this.windowWidth <= 992)
    }
  }
}
</script>

<style scoped>

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  z-index: 9997;
  transition: all 0.5s;
  padding: 30px;
  overflow-y: auto;
}

.nav-item a {
  display: flex;
  align-items: center;
  color: #45505b;
  padding: 10px 18px;
  margin-bottom: 8px;
  transition: 0.3s;
  font-size: 15px;
  border-radius: 50px;
  background: #f2f3f5;
  height: 56px;
  width: 56px;
  overflow: hidden;
}

.nav-item a:hover {
  color: #fff;
  background: #0563bb;
  width: 130px;
}

.nav-item .active {
  color: #fff;
  background: #0563bb;
}

.nav-item span {
  margin-left: 20px;
}

.mobile-nav-toggle {
  position: fixed;
  right: 15px;
  top: 15px;
  z-index: 9998;
  border: 0;
  background: none;
  font-size: 24px;
  transition: all 0.4s;
  outline: none !important;
  line-height: 1;
  cursor: pointer;
  text-align: right;
}

@media (max-width: 992px) {
  .nav-item span {
    margin-left: 10px;
  }

  .navbar {
    width: 220px;
    background: #fff;
    border-right: 1px solid #e6e9ec;
    left: -220px;
  }

  .slideIn{
    transition: 1s;
    left: 0 !important;
  }
  .nav-item a {
    width: 150px;
  }
}

@media (min-width: 992px) {
  .d-xl-none {
    display: none !important;
  }
}
</style>
