<template>
  <section id="portfolio">
    <div class="container aos-init aos-animate" data-aos="fade-up">
      <!-- Title -->
      <div class="section-title">
        <h2>GitHub Project</h2>
        <p>
          The following are my ongoing project/completed project on GitHub, including different front-end (Vue, jQuery, JavaScript)
          <br />and back-end (PHP, Laravel, Socket, Scrapping) Projects. Please check more on my GitHub 
        </p>
      </div>

      <!-- Card Container -->
      <div class="row">
        <!-- Card Item -->
        <div
          v-for="(repo, index) in repoData.slice(0,6)"
          :key="index"
          class="col-lg-4 col-md-6 d-flex align-items-stretch mt-4 aos-init aos-animate"
          data-aos="zoom-in"
          data-aos-delay="100"
        >
          <div class="icon-box" @click="clicked(index)">
            <img
              @error="logoError"
              :src="'https://raw.githubusercontent.com/'+ repo.full_name + '/master/logo.png'"
              alt="Image"
              class="card-img-top"
            />
            <div class="icon-box-container">
              <h4>
                <b-avatar variant="info" :src="repo.owner.avatar_url"></b-avatar>
                <a>{{repo.name}}</a>
              </h4>
              <p>{{repo.description}}</p>
              <b-badge variant="success">Available</b-badge>
              <b-badge variant="info">{{repo.language}}</b-badge>
              <b-badge variant="secondary">{{repo.license}}</b-badge>
              <b-badge variant="dark">
                {{repo.stargazers_count}}
                <font-awesome-icon color="yellow" :icon="['fas', 'star']" />
              </b-badge>
              <a ref="repoURL" :href="repo.html_url" style="float:right">
                <font-awesome-icon :icon="['fas', 'external-link-alt']" size="lg" />
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data () {
    return {
      repoData: []
    }
  },
  methods: {
    clicked (index) {
      this.$refs.repoURL[index].click()
    },
    logoError (event) {
      event.target.src = require('@/assets/img/github-logo.png')
    }
  },
  mounted () {
    fetch('https://api.github.com/users/stevetsim/repos?sort=updated', {
      method: 'get'
    })
      .then(response => {
        return response.json()
      })
      .then(jsonData => {
        if (jsonData) {
          this.repoData = jsonData
        }
      })
  }
}
</script>

<style scoped>
section {
  padding: 60px 0;
  overflow: hidden;
}

.section-title {
  text-align: center;
  padding-bottom: 30px;
}

.section-title h2 {
  font-size: 32px;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
  padding-bottom: 20px;
  position: relative;
  color: #45505b;
}

.section-title h2::before {
  content: "";
  position: absolute;
  display: block;
  width: 120px;
  height: 1px;
  background: #ddd;
  bottom: 1px;
  left: calc(50% - 60px);
}

.section-title h2::after {
  content: "";
  position: absolute;
  display: block;
  width: 40px;
  height: 3px;
  background: #0563bb;
  bottom: 0;
  left: calc(50% - 20px);
}

.section-title p {
  margin-bottom: 0;
}

.icon-box {
  transition: all ease-in-out 0.3s;
  background: #fff;
  box-shadow: 0px 5px 30px 0px rgba(110, 123, 131, 0.05);
  border-radius: 10px;
}

.icon-box-container {
  padding: 10px 20px 20px 20px;
}

.icon-box:hover {
  cursor: pointer;
  box-shadow: 0px 5px 10px 0px rgba(110, 123, 131, 0.397);
}

.card-img-top {
  height: 183.75px;
}

@media (max-width: 992px) {
  .card-img-top {
    height: auto;
  }
}

.icon-box h4 {
  font-weight: 600;
  margin: 10px 0 15px 0;
  font-size: 22px;
  width: 310px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.icon-box h4 a {
  color: #45505b;
  transition: ease-in-out 0.3s;
}

.icon-box p {
  line-height: 24px;
  font-size: 14px;
  margin-bottom: 10px;
  width: 310px;
  overflow: hidden;
  white-space: normal;
  text-overflow: ellipsis;
  word-break: break-all;
  height: 50px;
}

.b-avatar {
  height: 1.5em !important;
  width: 1.5em !important;
  margin-right: 10px;
}

.badge {
  margin-right: 2px;
}
</style>
